import React, { useGlobal } from 'reactn';
import { makeStyles } from '@material-ui/core/styles';
import posed from 'react-pose';



const Box = posed.div({
    draggable: true,
    init: { scale: 1 },
    drag: { scale: 1.2 },
    dragEnd: { scale: 1 },
})

const Label = posed.span({
    init: { color: '#000' },
    drag: { color: '#f00' }
})

const divStyle = {
    borderStyle: 'solid',
    borderColor: 'coral'
}

export function Marker(props) {
    const useStyles = makeStyles(theme => ({    
        root: {
            color: '#fff',
            fontSize: '32px',
            fontFamily: 'PT Sans, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif',
            fontWeight: 200,
            textShadow: '2px 2px 0px rgba(0, 0, 0, 0.2)',
            width: '100%',
            alignItems: 'center',
            justifyContent: 'center',
            display: 'flex'
        },
        box: {
            width: props.marker.width,
            height: props.marker.height,
            borderRadius: '100%',
            background: '#fff',
            alignItems: 'center',
            justifyContent: 'center',
            display: 'flex',
            right: '5px,', /*newly added*/
            top: '5px',
            position: 'absolute',
            zIndex: 1,
            opacity: 0.5,
            filter: 'alpha(opacity=50)' /* For IE8 and earlier */
        }
    }))
    const { id, selected } = props
    const classes = useStyles()
    const [selectedMarker, setSelectedMarker] = useGlobal('selectedMarker')
    
    return (
        <Box className={classes.box} id={id} style={{ borderStyle: selected ? 'solid' : 'none', borderColor: 'coral' }} onDragEnd={() => { setSelectedMarker(id) }}>
            <Label className="label">{props.marker.lession}</Label>
        </Box>
    )
}