import React, { useState, useGlobal } from 'reactn'
import { withStyles, makeStyles } from '@material-ui/core/styles'
import { Card, CardContent, CardHeader, Slider } from '@material-ui/core'

const useStyles = makeStyles(theme => ({   
  card: {
      minWidth: 200,
  },    
  title: {
      fontSize: 14,
  },
}));

export function WatchTeacher (props) {
  const classes = useStyles();  
  const [markers, setMarkers] = useGlobal('markers')  
  const [selectedMarker, setSelectedMarker] = useGlobal('selectedMarker')

  const PrettoSlider = withStyles({
      root: {
        color: '#52af77',
        height: 8,
      },
      thumb: {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        marginTop: -8,
        marginLeft: -12,
        '&:focus,&:hover,&$active': {
          boxShadow: 'inherit',
        },
      },
      active: {},
      valueLabel: {
        left: 'calc(-50% + 4px)',
      },
      track: {
        height: 8,
        borderRadius: 4,
      },
      rail: {
        height: 8,
        borderRadius: 4,
      }
  })(Slider);

  function handleChangeHeight (event, value) {
    const marker = markers[selectedMarker]
    marker.height = value   
    setMarkers(markers)
  }

  function handleChangeWidth (event, value) {
    const marker = markers[selectedMarker]
    marker.width = value
    setMarkers(markers)
  }
  if (markers && selectedMarker !== undefined) {
    const marker = markers[selectedMarker]
    return (
      <Card className={ classes.card }>
        <CardHeader title={ marker.lession } />              
        <CardContent>
          height
          <PrettoSlider 
            valueLabelDisplay="auto" 
            aria-label="pretto slider" 
            defaultValue={marker ? marker.height : 100} 
            max={600} 
            onChangeCommitted={ handleChangeHeight }
          />
          width
          <PrettoSlider 
            valueLabelDisplay="auto" 
            aria-label="pretto slider" 
            defaultValue={marker ? marker.width : 100} 
            max={600} 
            onChangeCommitted={ handleChangeWidth }
          />
        </CardContent>
      </Card> 
    )
  } else {
    return (<></>)
  }
}